/* Screen Sizes */
@screen-xxxs-max: 319px;
@screen-xxs-min: 320px;
@screen-xxs-max: 479px;
@screen-xs-min: 480px;
@screen-xs-max: 767px;
@screen-sm-min: 768px;
@screen-sm-max: 991px;
@screen-md-min: 992px;
@screen-md-max: 1199px;
@screen-lg-min: 1200px;

// Default Colors
@sidebar-black: #2F3334;
@sidebar-white: #FAFAFA;
@sidebar-smoke: #F1F1F1;

.without-state-color {
  color: #000000 !important;
}

.sidebar-black {
  color: @sidebar-black;
}

.sidebar-white {
  color: @sidebar-white;
}

.sidebar-smoke {
  color: @sidebar-smoke;
}

/* Imports */
@import './cover.less';
@import './sidebar.less';

/* Vendor */
.vendor(@property, @value) {
     -moz-@{property}: @value;
      -ms-@{property}: @value;
       -o-@{property}: @value;
  -webkit-@{property}: @value;
          @{property}: @value;
}

/* General */
body {
  overflow-x: hidden;

  &.plone-toolbar-left {
    padding-left: 0;
  }

  #edit-zone {
    display: none;
  }

  &.plone-toolbar-left-expanded,
  &.plone-toolbar-left-default {
    padding-left: 0 !important;
  }
}

